<template>
    <v-col cols="12">
    <v-row>
      <v-col>
      <v-dialog 
        v-if="!dataSent"
        cols="12"
        v-model="dialog"  
        max-width="700"
        theme="dark"
        aling="end"
        >
        <template v-slot:activator="{ props }">
          <v-btn
            aling="end"
            class="bg-indigo" 
            v-bind="props"
          ><v-icon class="mr-2">mdi-plus-circle</v-icon>
            Fazer pedido
          </v-btn>
        </template>
        <v-card>
          <v-col cols="12">
            <v-row>
              <v-col cols="12" sm="12" class="mb-3">
                <v-card-title class="headline text-primary">
                  <span class="text-h5"><strong>Faça seu pedido de oração</strong></span>
                </v-card-title>
                <v-card-subtitle>
                  <h5>
                    Preencha seus dados para receber a sua oração

                  </h5>
                </v-card-subtitle>
              </v-col>
            </v-row>
            <v-form>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6" class="mb-2">
                    <v-row>
                      <v-col cols="12" class="pb-1 pt-0">Nome*</v-col>
                      <v-col cols="12" class="pt-0">
                        <v-text-field
                          v-model="request.name" 
                          placeholder="Ex: João da Silva"
                          maxlength="25"
                          :counter="25"
                          required
                          :rules="[v => !!v || 'Campo Nome é obrigatório',
                          v => (v && v.length >= 6 || 'Mínimo de 6 caracteres')
                          ]"
                        >
                        </v-text-field>
                      </v-col>
                    </v-row>
                  </v-col>

                  <v-col cols="12" sm="6" class="mb-2">
                    <v-row>
                      <v-col cols="12" class="pb-1 pt-0">Celular*</v-col>
                      <v-col cols="12" class="pt-0">
                        <v-text-field
                          v-model="request.contact" 
                          required
                          class="no-spin-buttons"
                          placeholder="(61) 9 9999-9999"
                          maxlength="16"
                          :counter="16"
                          :rules="[v => !!v || 'Campo Celular é obrigatório',
                           v => /^\d+$/.test(v) || 'Somente números são permitidos',
                           v => (v && v.length >= 9 || 'Mínimo de 9 caracteres')
                           ]"
                        >
                        </v-text-field>
                      </v-col>
                    </v-row>
                  </v-col>

                  <v-col cols="12" sm="6" class="mb-2">
                    <v-row>
                    
                      <v-col cols="12" class="pb-1 pt-0">Email:</v-col>
                      <v-col cols="12" class="pt-0">
                        <v-text-field v-model="request.email" 
                          required 
                          outlined 
                          dense 
                          placeholder="joao@exemple.com"
                          maxlength="40"
                          :rules="[v => !v || /.+@.+\..+/.test(v) || 'Formato de email incorreto']"
                          >
                        </v-text-field>
                      </v-col>
                    </v-row>
                  </v-col>

                  <v-col cols="12" sm="6" class="mb-2">
                    <v-row>
                      <v-col cols="12" class="pb-1 pt-0">Como deseja receber a oração?</v-col>
                      <v-col cols="12" class="pt-0">
                        <v-select 
                          v-model="request.kind" 
                          :items="['Telefone/ celular', 'Mensagem via whatsapp']" 
                          label="Selecione"
                          required
                          :rules="[v => !!v || 'Campo Método de Recebimento é obrigatório']">
                        </v-select>
                      </v-col>
                    </v-row>
                  </v-col>
                  <v-col cols="12" sm="12" class="mb-2">
                    <v-row>
                      <v-col cols="12">Deixe aqui o seu pedido de oração*</v-col>
                      <v-col cols="12" class="pt-0">
                        <v-textarea 
                          v-model="request.prayerRequest" 
                          required
                          placeholder="Escreva o seu pedido" 
                          :rules="[v => !!v || 'Campo Pedido de Oração é obrigatório',
                          v => (v && v.length >= 10) || 'Mínimo de 10 caracteres']"
                          maxlength="256"
                          :counter="256"
                        >
                        </v-textarea>
                      </v-col>
                    </v-row>
                  </v-col>
                  <v-col>
                    <v-row>
                      <v-checkbox v-model="request.accepted">
                        <template v-slot:label>
                          <div :rules="[v => !!v || 'Campo Pedido de Oração é obrigatório']">
                            Concordo em enviar meus dados e contatos para este fim*
                          </div>
                        </template>
                      </v-checkbox>
                    </v-row>
                  </v-col>
                </v-row>
              </v-container>
            </v-form>
            <v-card-actions>
              <v-spacer></v-spacer>
                <v-btn
                  color="red"
                  @click="dialog = false" 
                >Fechar
                </v-btn>
                <v-btn
                  color="white"
                  style="background-color: rgb(66, 106, 254)" 
                  @click="sendRequest"
                  :disabled="isDesabledSend"
                >
                  Enviar pedido
                </v-btn>
            </v-card-actions>
          </v-col>
        </v-card>
      </v-dialog>
    </v-col>
    </v-row>
  </v-col>
  <v-col>
    <div v-if="dataSent" class="text-left">
      <v-alert
        type="success"
        title="Enviado"
        text="Seu pedido foi registrado com sucesso!"
    >
      </v-alert>
    </div>
  </v-col>
</template>

<script>
import axios from 'axios'

export default {
  data: () => ({
    dialog: true,
    dataSent: false,
    request: {
      name: null,
      contact: '',
      email: '',
      prayerRequest: '',
      kind: '',
      accepted: false
    }
  }),
  methods: {
    sendRequest() {
        axios.post('https://pray-for-me-19a70-default-rtdb.firebaseio.com/user.json', this.request)
          .then(resp => {
            this.request = resp.data;
            this.dialog = false
            console.log(resp.data)
            this.request = ''
            this.dataSent = true
          })
          .catch(error => {
            error.data
          })
      }   
  },
  computed: {
    isDesabledSend() {
      return (
        !this.request.name ||
        !this.request.contact ||
        !this.request.prayerRequest||
        !this.request.accepted
      )
    }
  }
}
</script>

<style>
  .no-spin-buttons input[type="number"]::-webkit-inner-spin-button,
  .no-spin-buttons input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
}
</style>